import { createCustomElement } from '@servicenow/ui-core';
import { transformMessages, isAttrTrue } from '@devsnc/sn-controls-common';
import { t } from 'sn-translate';
import { isFunction } from 'lodash';
import { adaptCallbackForLegacyAPI } from '@devsnc/sn-controls-common';

import '@servicenow/now-input';

export const validUrlPattern =
	'^$|^([hH][tT][tT][pP][sS]?|[fF][tT][pP]):\\/\\/([[wW][wW][wW]\\.)?(([-\\w@:%.\\+~#=]{1,256}\\.[a-zA-Z0-9()]{1,63})|([lL][oO][cC][aA][lL][hH][oO][sS][tT]))\\b([-\\w()@:%\\+.~#?&//=]*)';

const URL = ({
	properties: {
		name,
		messages,
		label,
		placeholder,
		required,
		readonly,
		initialValue,
		invalid,
		value = initialValue
	}
}) => {
	// Missing:
	// Focus on input after going into edit mode
	// Focus on edit button after leaving edit mode
	// Customizable pattern for url validation

	//	config-aria={{
	//		'aria-controls': this.props.componentId,
	//		'aria-label': t(
	//			'Field action for {0}: {1}',
	//			this.props.label,
	//			editLabel
	//		)
	//	}}
	if (isAttrTrue(readonly) && !value)
		return <now-input label={label} readonly={true} value="â€”" />;

	return (
		<now-input-url
			style={{ display: 'inline' }}
			pattern={validUrlPattern}
			name={name}
			value={value}
			messages={transformMessages(messages)}
			label={label}
			placeholder={placeholder}
			required={required}
			readonly={readonly}
			invalid={invalid}
		/>
	);
};

createCustomElement('sn-record-url', {
	view: URL,
	properties: {
		description: {},
		initialValue: {},
		invalid: {},
		label: {},
		messages: {},
		name: {},
		placeholder: {},
		required: {},
		readonly: {},
		reversed: {},
		value: {},

		onValueChange: {},
		onStagedValueChange: {}
	},
	actions: {
		'NOW_INPUT_URL#VALUE_SET': { stopPropagation: true },
		'NOW_INPUT_URL#INVALID_SET': { stopPropagation: true }
	},
	actionHandlers: {
		'NOW_INPUT_URL#INVALID_SET': ({
			action: {
				payload: { name, fieldValue: value, value: invalid }
			},
			properties: { onValueChange }
		}) => {
			const error = !invalid
				? null
				: {
						type: 'error',
						message: t(
							'The URL provided is invalid. Please provide the absolute URL.'
						)
				  };
			if (isFunction(onValueChange)) {
				adaptCallbackForLegacyAPI(onValueChange)({ name, value, error });
			}
		}
	}
});

export default URL;
